h1,
h2,
h3,
h4,
h5 {
	font-weight: bold;
}

button {
	@apply px-12 py-4 font-bold bg-blue text-white rounded;
	border: 2px solid;
	transition: $transition;
	&:hover {
		@apply border-blue;
	}
}

nav li {
	margin-left: 38px;
	&::after {
		content: "";
		display: block;
		position: relative;
		top: 0.5rem;
		width: 0;
		opacity: 0;
		border: 2px solid $dark;
		transition: 0.15s width ease-in-out;
	}
	&:hover {
		&::after {
			opacity: 1;
			width: 75%;
		}
	}
}

/* underline - hover.css */
.underline {
	display: inline-block;
	vertical-align: middle;
	transform: perspective(1px) translateZ(0);
	position: relative;
}
.underline:before {
	content: "";
	position: absolute;
	z-index: -1;
	left: 51%;
	right: 51%;
	bottom: 0;
	background: $theme;
	height: 4px;
	transition-property: left, right;
	transition-duration: 0.15s;
	transition-timing-function: ease-in-out;
}
.underline:hover:before,
.underline:focus:before,
.underline:active:before {
	left: -1rem;
	right: -1rem;
}

@media (max-width: 1400px) {
	.container {
		width: 100% !important;
		max-width: none;
	}
	#overview_container {
		background-size: 100% auto;
	}
	.card:not(:last-of-type) {
		margin-right: 1rem;
	}
}

@media (max-width: 1024px) {
	#overview_container {
		background-image: none;
	}
	footer {
		h1 {
			a {
				margin-left: 0;
			}
		}
	}
}

@media (min-width: 1024px) {
	.card {
		margin-bottom: 0;
		width: 340px;
	}
}

section {
	p {
		@apply text-s20;
		font-weight: 300;
	}
	h1 {
		@apply pb-8 text-s46;
		display: inline-block;
	}
}
