.content {
	@apply p-4;
	h3 {
		@apply text-dark mb-4 text-s24;
	}
	p {
		@apply font-light;
		font-size: 1rem;
	}
}

.card {
	@apply rounded bg-lighter overflow-hidden mb-16;
	width: 100%;
	box-shadow: $shadow;
	position: relative;
	transition: $transition;
	bottom: 0;
	.card-image {
		max-height: 170px;
		display: inline-block;
		overflow: hidden;
		width: 100%;
		div {
			max-height: 170px;
		}
	}
	h3,
	img {
		transition: $transition;
	}
	img {
		width: 100%;
		max-height: 180px;
		object-fit: cover;
	}
	p {
		margin-bottom: 3rem;
	}
	.more {
		position: absolute;
		bottom: 1rem;
		right: 0.5rem;
		a {
			@apply mt-2 px-4 py-2;
			&::after {
				margin-left: 10px;
				width: 9px;
				height: 15px;
				content: "";
				mask: url("../images/arrow.svg");
				mask-size: cover;
				display: inline-block;
				position: relative;
				top: 2px;
				color: inherit;
				background-color: $dark;
				transition: $transition;
			}
		}
	}
	// When you hover things go whoosh!
	&:hover {
		bottom: 0px;
		box-shadow: $shadow-lg;
		color: white;
		background-color: $theme;
		h3 {
			color: white;
		}
		img {
			transform: scale(1.05);
		}
		.more a::after {
			background-color: white;
		}
	}
}

// More on Medium Button
#more {
	@apply mt-8 mb-4 border-blue text-blue py-3;
	background-color: transparent;
	&:hover {
		color: white;
		background-color: $theme;
	}
}
