  font-weight:700;
  --bg-opacity:1;
  background-color:#2F55D4;
  background-color:rgba(47, 85, 212, var(--bg-opacity));
  --text-opacity:1;
  color:#fff;
  color:rgba(255, 255, 255, var(--text-opacity));
  border-radius:10px;
  border:2px solid;
  transition:all 0.25s ease-in-out
}

button:hover{
  --border-opacity:1;
  border-color:#2F55D4;
