}

nav li{
  margin-left:38px
}

nav li::after{
  content:"";
  display:block;
  position:relative;
  top:0.5rem;
  width:0;
  opacity:0;
  border:2px solid #102a3d;
  transition:0.15s width ease-in-out
}

nav li:hover::after{
  opacity:1;
  width:75%
}

.underline{
  display:inline-block;
  vertical-align:middle;
  transform:perspective(1px) translateZ(0);
  position:relative
}

.underline:before{
  content:"";
  position:absolute;
  z-index:-1;
  left:51%;
  right:51%;
  bottom:0;
  background:#2f55d4;
  height:4px;
  transition-property:left, right;
  transition-duration:0.15s;
  transition-timing-function:ease-in-out
}

.underline:hover:before,.underline:focus:before,.underline:active:before{
  left:-1rem;
  right:-1rem
}

