section p{
  font-size:1.25rem;
  font-weight:300
}

section h1{
  padding-bottom:2rem;
  font-size:2.88rem;
  display:inline-block
}

.content{
  padding:1rem
}

.content h3{
  --text-opacity:1;
  color:#102A3D;
  color:rgba(16, 42, 61, var(--text-opacity));
  margin-bottom:1rem;
  font-size:1.5rem
}

