.card{
  border-radius:10px;
  --bg-opacity:1;
  background-color:#F9FBFF;
  background-color:rgba(249, 251, 255, var(--bg-opacity));
  overflow:hidden;
  margin-bottom:4rem;
  width:100%;
  box-shadow:0 8px 30px rgba(47,85,212,0.12);
  position:relative;
  transition:all 0.25s ease-in-out;
  bottom:0
}

.card .card-image{
  max-height:170px;
  display:inline-block;
  overflow:hidden;
  width:100%
}

.card .card-image div{
  max-height:170px
}

.card h3,.card img{
  transition:all 0.25s ease-in-out
}

.card img{
  width:100%;
  max-height:180px;
  object-fit:cover
}

.card p{
  margin-bottom:3rem
}

.card .more{
  position:absolute;
  bottom:1rem;
  right:0.5rem
}

.card .more a{
  margin-top:0.5rem;
  padding-left:1rem;
  padding-right:1rem;
  padding-top:0.5rem;
  padding-bottom:0.5rem
}

.card .more a::after{
  margin-left:10px;
  width:9px;
  height:15px;
  content:"";
  mask:url("../images/arrow.svg");
  mask-size:cover;
  display:inline-block;
  position:relative;
  top:2px;
  color:inherit;
  background-color:#102a3d;
  transition:all 0.25s ease-in-out
}

.card:hover{
  bottom:0px;
  box-shadow:0 8px 30px rgba(47,85,212,0.25);
  color:white;
  background-color:#2f55d4
}

.card:hover h3{
  color:white
}

.card:hover img{
  transform:scale(1.05)
}

.card:hover .more a::after{
  background-color:white
}

