footer {
	h1 {
		a {
			padding: 2px 1rem;
			margin-left: -1rem;
			border-bottom: 4px solid $theme;
			background: transparent;
			transition: $transition;
			&:hover {
				margin-left: 0;
				@apply text-white;
				background-color: $theme;
			}
		}
	}
	.copyright {
		opacity: 0.75;
		font-weight: 300;
		transition: $transition;
		a:hover {
			&::before,
			&::after {
				content: "üç∞";
			}
			&::before {
				margin-right: 5px;
			}
			&::after {
				margin-left: 5px;
			}
		}
	}
}
.socials {
	ul {
		@apply my-10;
		li:not(:first-of-type) {
			@apply pl-12;
		}
	}
}
